<template>
  <div>
    <div style="height:100vh;" class="d-flex justify-content-center align-items-center ">
        <div style="width:100%">
          <div class="row justify-content-center">
            <div class="col-10">
              <div class="shadow p-3 rounded-lg">
              <div class="text-xl">
                <button type="button" @click="$router.go(-1)" class="btn btn-sm btn-dark  "><span class="typcn typcn-arrow-left"></span> go Back</button>
                <br>
                <br>
                Ganti Password! 
                  <span class="font-semibold p-2">
                    {{$store.state.users.email}}
                  </span>
                </div>
                <div class="sm-form ">
                    <label for="password">password</label>
                    <input type="password" id="password" name="password" class="form-control form-control-sm" placeholder="password" v-model="vdata.password" >
                </div>
                <div class="sm-form ">
                    <label for="password">re password</label>
                    <input type="password" id="password" name="password" class="form-control form-control-sm" placeholder="password" v-model="vdata.password2" >
                </div>
                <p class="p-2 font-bold text-red" v-show="vdata.password!=vdata.password2">Password Tidak Sama!</p>
                <button type="button" @click="changePasswword()" class="btn btn-sm btn-primary  "><span class="typcn typcn-key"></span> Change Password</button>
                <hr class="">
                <button type="button" class="btn btn-sm btn-dark " @click="$router.go(-1)"><span class="typcn typcn-arrow-left"></span> Back</button>
                <button type="button" @click="$store.dispatch('logout')" class="btn btn-sm btn-dark float-right">Logout</button>
              </div>
              
            </div>
          </div>
        </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import firebase from "firebase";
let db = firebase.firestore();
export default {
  data() {
    return {
        vdata:{}
    };
  },
  methods: {
    changePasswword() {
      let that=this;
      let user = firebase.auth().currentUser;
      console.log(user)
      let newPassword = this.vdata.password;

      user.updatePassword(newPassword).then(
        () => {
          db.collection('users').doc(user.uid).set({changepassword:'false'},{merge:true}).then(res=>{
            firebase.auth().signOut().then(() => {
                console.log('logout berhasil')
                that.$router.push('/')
                }).catch((error) => {
            });
          })
          // Update successful.
        },
        error => {
            alert('gagal ganti password')
          // An error happened.
        }
      );
    }
  },
  beforeMount() {
    this.$store.dispatch('ceklogin',{true:true,false:'/auth/login'});
  },
  mounted() {}
};
</script>